<template lang="pug">
#app
  TheHeader(:title="title", @open-setting="openSetting")
  main
    .contain
      .options
        .upload-wrap(@click="$refs.upload.click()")
          input#upload(ref="upload", type="file", @change="onFileSelect")
          img.preview(v-if="previewUrl", :src="previewUrl")
          i.placeholder.mdi.mdi-image(v-else)
        button.submit(@click="uploadImage")
          i.mdi.mdi-upload.light
      VProgress(v-show="loading")
      .info
        ul(v-if="img.sourceUrl")
          li(v-for="(info, key) in img", :key="key")
            .info-name {{ info.detail }}
            a(v-if="value.search('http')", :href="value") {{ value }}
            | {{ info.detail }}: {{ info.value }}
  TheDrawer(v-model="openDrawer")
    ul.config-info
      li.config-item(v-for="(value, key) in config", :key="key") 
        .config-name {{ key.toUpperCase() }}
        .config.value {{ value }}
  TheFooter
</template>

<script>
import TheHeader from "@/components/TheHeader.vue";
import TheFooter from "@/components/TheFooter.vue";
import TheDrawer from "@/components/TheDrawer.vue";
import VProgress from "@/components/VProgress.vue";
import File from "@/assets/script/file.js";

export default {
  name: "App",
  components: {
    TheHeader,
    TheFooter,
    TheDrawer,
    VProgress,
  },
  data: () => ({
    title: "NLOLI HOST",
    openDrawer: false,
    loading: false,
    img: {},
    previewUrl: null,
  }),
  created() {
    console.log(this.config);
  },
  computed: {
    config() {
      return {
        token,
        repo,
        branch,
        prefix,
        username,
        email,
      };
    },
  },
  methods: {
    openSetting() {
      this.openDrawer = true;
    },
    /**
     *
     */

    onFileSelect(e) {
      this.previewUrl = URL.createObjectURL(e.target.files[0]);
    },
    asCdnUrl(url) {
      return url
        .replace("raw.githubusercontent.com", "cdn.jsdelivr.net/gh")
        .replace(`${branch}/`, "");
    },
    async getInfo() {
      // INFO
      // https://api.github.com/repos/${repo}/contents/${prefix}${fileName}?ref=main
    },
    // 图片上传到Github，并使用CDN提升加载速度
    async uploadImage() {
      try {
        this.loading = true;
        // 获取图片文件
        const uploadEl = document.getElementById("upload");
        if (!uploadEl || !uploadEl.files || !uploadEl.files[0]) return;
        const file = uploadEl.files[0];
        this.previewUrl = URL.createObjectURL(file);
        // 验证文件类型
        if (!/image\/\w+/.test(file.type)) {
          alert("无法识别该图片！");
          return false;
        }
        // 转换文件为Base64
        const base64Data = await File.readFileAsBase64(file);
        // 计算MD5
        const md5 = await File.getMd5(file);
        // 拼接文件名
        const fileName = `${md5}.${file.type.replace(/.*\//, "")}`;
        // 创建Json对象
        const data = {
          committer: { name: username, email: email },
          message: `upload image ${fileName}`,
          // 图片编码为纯Base64
          content: base64Data,
        };
        /* 开始上传 */
        // 上传图片到Github仓库
        const resp = await fetch(
          `https://api.github.com/repos/${repo}/contents/${prefix}${fileName}?access_token=${token}`,
          {
            method: "PUT",
            body: JSON.stringify(data),
            headers: {
              "Content-Type": "application/json",
              Authorization: `token ${token}`,
            },
          }
        );
        if (resp.ok) {
          // 上传成功
          const result = await resp.json();
          // 修改仓库原始url为CDN url，更新url到表单
          const content = {
            name: {
              detail: "文件名",
              value: result.content.name,
            },
            sha: {
              detail: "SHA",
              value: result.content.sha,
            },
            size: {
              detail: "文件大小",
              value: result.content.size,
            },
            sourceUrl: {
              detail: "来源",
              value: `https://raw.githubusercontent.com/nyarray/LoliHost/main/upload/${fileName}`,
            },
            cdnUrl: {
              detail: "CDN加速",
              value: this.asCdnUrl(result.content.download_url),
            },
          };
          this.img = content;
        } else if (resp.status === 422) {
          // 返回422代表图片已存在，重复上传
          const content = {
            sourceUrl: {
              detail: "来源",
              value: `https://raw.githubusercontent.com/nyarray/LoliHost/main/upload/${fileName}`,
            },
            cdnUrl: {
              detail: "CDN加速",
              value: this.asCdnUrl(
                `https://raw.githubusercontent.com/nyarray/LoliHost/main/upload/${fileName}`
              ),
            },
          };
          this.img = content;
        } else {
          alert("图片上传失败");
        }
      } catch (e) {
        alert(`图片上传失败：${e}`);
      } finally {
        this.loading = false;
      }
    },
  },
};
</script>

