<template lang="pug">
.nav-drawer(:class="{ open: show }")
  .drawer-blank(@click="$emit('change', !show)")
  .drawer-main
    .drawer-banner 
    ul.drawer-body(@click="$emit('change', !show)")
      slot
</template>


<script>
export default {
  model: {
    prop: "show",
    event: "change",
  },
  props: {
    show: {
      type: Boolean,
      default: false,
    },
  },
  data: () => ({
    drawer: false,
  }),
  methods: {},
};
</script>

<style lang="less" scoped>
.nav-drawer {
  .drawer-main {
    position: fixed;
    top: 0;
    right: -20rem;
    width: 20rem;
    height: 100%;
    transition: right 0.3s ease;
    background-color: white;//var(--theme);
    color: black;
    z-index: 100;

    .drawer-banner {
      height: 22vh;
      // background-image: url(https://cdn.jsdelivr.net/gh/tsukiseele/ImageHosting/upload/826f66f94e3ebf1f62cff7c9109bb118.jpeg);
      background-repeat: no-repeat;
      background-size: cover;
      background-position: center;
    }
  }

  .drawer-blank {
    position: fixed;
    display: none;
    height: 100%;
    width: 100%;
    top: 0;
    z-index: 99;
    transition: background 0.3s ease;
  }

  &.open {
    .drawer-main {
      right: 0%;
      box-shadow: 0 -3px 6px gray;
    }
    .drawer-blank {
      display: block;
      background-color: rgba(0, 0, 0, 0.67);
    }
  }
}
</style>